import { AfterViewInit, Component, ElementRef, Input, OnInit, QueryList, SecurityContext, ViewChild, ViewChildren } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import clock24Regular from '@iconify-icons/fluent/clock-24-regular';
import location28Regular from '@iconify-icons/fluent/location-28-regular';
import { IconService } from '@visurel/iconify-angular';
import { gsap } from 'gsap';
import colorparse from 'color-parse';
import ScrollToPlugin from 'gsap/ScrollToPlugin';

@Component({
  selector: 'app-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.scss']
})
export class BannerComponent implements OnInit, AfterViewInit {

  @ViewChild('containerEl')
  containerEl: ElementRef;
  @ViewChild('eventsEl')
  eventsEl: ElementRef;
  @ViewChildren('itemEl')
  itemsEl: QueryList<ElementRef>;

  @Input() events: any;
  @Input() width: number;
  @Input() height: number;

  bodyStyle: string;
  rowColor = '';
  altColor = '';
  dy = 100;
  y = 0;
  visible = 1;
  max = 0;

  constructor(
    private iconService: IconService,
    private domSanitizer: DomSanitizer) {
    iconService.register('clock', clock24Regular);
    iconService.register('location', location28Regular);
    gsap.registerPlugin(ScrollToPlugin); // Register ScrollTo plugin

    if (typeof gadgets !== 'undefined') {
      let prefs = new gadgets.Prefs();
      this.bodyStyle = prefs.getString('body-style');
      this.rowColor = prefs.getString('row-color');
      this.altColor = prefs.getString('alt-color');
    }
  }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {

    if (this.bodyStyle) {
      this.eventsEl.nativeElement.setAttribute('style',
        this.domSanitizer.sanitize(SecurityContext.HTML, this.domSanitizer.bypassSecurityTrustHtml(this.bodyStyle.replace(/&#34;/g, "'"))))
    }
  }

  getBackgroundColor(odd: boolean) {

    if (odd && this.altColor !== '') {
      return this.altColor;
    }
    return this.rowColor;
  }

  public scroll() {

    if (this.itemsEl.length) {
      this.dy = this.itemsEl.first.nativeElement.offsetHeight;
      this.visible = Math.floor(this.containerEl.nativeElement.offsetHeight / this.dy);
      this.max = this.itemsEl.length - this.visible + 1;
    }

    //const topY = this.containerEl.nativeElement.getBoundingClientRect().top;

    this.y += 1;
    if (this.y >= this.max) {
      this.y = 0;
    }

    gsap.to(this.containerEl.nativeElement, {
      duration: 1.2, ease: "power4.inOut", scrollTo: '#item-' + this.y
    });
  }
}
