import { getLocaleDirection } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { PlayerClientService } from '@reveldigital/player-client';
import * as moment from 'moment-timezone';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ICalService {

  DATEONLY_REGEX = /^([12]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01]))$/;
  futureEvents = '30';

  constructor(private http: HttpClient, private client: PlayerClientService) {

    this.client.getDeviceTimeZoneName().then((res) => {
      if (res) {
        moment.tz.setDefault(res);
      } else {
        moment.tz.setDefault('America/Chicago');
      }
    })

    if (typeof gadgets !== 'undefined') {
      let prefs = new gadgets.Prefs();
      this.futureEvents = prefs.getString("future-events");
    }
  }

  getEvents(calendarUrl: any): Observable<any> {

    // DEVELOPMENT VARAIBLES

    // private TZName: any;
    // private urlPref = new gadgets.Prefs().getString('url');
    // private timePref = new gadgets.Prefs().getString('timeRange');
    // private url = `https://glacial-hollows-70580.herokuapp.com/ical/2020-09-29T00:00:00.000Z/2020-10-15T23:00:00.000Z?url=${this.urlPref}`;

    const now = moment().format('YYYY-MM-DDThh:mm');

    const endDateRange = (d) => {
      if (d === '365') {
        return moment().add(1, 'y').format('YYYY-MM-DDThh:mm');
      } else if (d === '0') {
        return moment().add(1, 'd').format('YYYY-MM-DDThh:mm');
      } else if (d === '7') {
        return moment().add(1, 'w').format('YYYY-MM-DDThh:mm');
      } else if (d === '30') {
        return moment().add(1, 'M').format('YYYY-MM-DDThh:mm');
      }
    };

    const url = `https://glacial-hollows-70580.herokuapp.com/ical/${now}/${endDateRange(this.futureEvents)}?url=${encodeURIComponent(calendarUrl)}`;

    return this.http.get<any>(url).pipe(
      map(data => this.mapEvents(data)));
  }

  mapEvents(data) {

    let events = [];

    if (data.occurrences) {
      events = events.concat(data.occurrences.map(o => ({
        startDate: o.startDate.isDate ?
          moment([o.startDate.year, o.startDate.month - 1, o.startDate.day], o.startDate.timezone === 'floating' ? null : o.startDate.timezone) :
          moment.tz([o.startDate.year, o.startDate.month - 1, o.startDate.day, o.startDate.hour, o.startDate.minute], o.startDate.timezone === 'floating' ? null : o.startDate.timezone),
        endDate: o.endDate.isDate ?
          moment.tz([o.endDate.year, o.endDate.month - 1, o.endDate.day], o.endDate.timezone === 'floating' ? null : o.endDate.timezone) :
          moment.tz([o.endDate.year, o.endDate.month - 1, o.endDate.day, o.endDate.hour, o.endDate.minute], o.endDate.timezone === 'floating' ? null : o.endDate.timezone),
        allDay: o.startDate.isDate,
        location: this.getProperty(o.item, 'location'),
        summary: this.getProperty(o.item, 'summary'),
        description: this.getProperty(o.item, 'description')
      })));
    }
    if (data.events) {
      events = events.concat(data.events.map(o => ({
        startDate: moment(this.getProperty(o, 'dtstart')),
        endDate: moment(this.getProperty(o, 'dtend')),
        location: this.getProperty(o, 'location'),
        summary: this.getProperty(o, 'summary'),
        description: this.getProperty(o, 'description'),
        allDay: this.DATEONLY_REGEX.test(this.getProperty(o, 'dtstart'))
      })));
    }

    return events.sort((a, b) => a.startDate - b.startDate);
  }

  getProperty(item, propName) {

    if (item && item.component[0] === 'vevent') {
      return item.component[1].find(o => o[0] === propName) ? item.component[1].find(o => o[0] === propName)[3] : null;
    }
    return '';
  }
}
