import { AfterContentInit, AfterViewInit, ChangeDetectorRef, Component, ElementRef, HostBinding, QueryList, SecurityContext, ViewChild, ViewChildren } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DomSanitizer } from '@angular/platform-browser';
import { Observable, timer } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { DateTime } from 'luxon';
import { gsap } from 'gsap';
import { BannerComponent } from './layouts/banner/banner.component';
import { TableComponent } from './layouts/table/table.component';
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import { IconService } from '@visurel/iconify-angular';
import location28Regular from '@iconify-icons/fluent/location-28-regular';
import clock24Regular from '@iconify-icons/fluent/clock-24-regular';
import { ICalService } from './ical.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  @ViewChild(BannerComponent) banner: BannerComponent;
  @ViewChild(TableComponent) table: TableComponent;

  @HostBinding('style.width.px')
  width: number = 800;
  @HostBinding('style.height.px')
  height: number = 500;

  layout = 'table';
  scrolling = true;
  interval = 2;
  sub: any;
  prefs: any;
  pollingInterval = 30 * 60 * 1000; // 30 minutes
  calUrl = 'https://calendar.google.com/calendar/ical/4t0mtlq6irm5dl32gp9euutajg%40group.calendar.google.com/public/basic.ics';
  events = [];


  constructor(
    private http: HttpClient,
    private domSanitizer: DomSanitizer,
    private iconService: IconService,
    private icalService: ICalService) {

    iconService.register('clock', clock24Regular);
    iconService.register('location', location28Regular);
    gsap.registerPlugin(ScrollToPlugin);

    if (typeof gadgets !== 'undefined') {
      this.prefs = new gadgets.Prefs();
      this.width = this.prefs.getInt('rdW');
      this.height = this.prefs.getInt('rdH');
      this.layout = this.prefs.getString('layout');
      this.scrolling = this.prefs.getBool('enable-scrolling');
      this.calUrl = this.prefs.getString('calendar-url');
      this.interval = this.prefs.getInt('scroll-interval');
    }
  }

  ngOnInit(): void {

    this.sub = this.startPolling(this.pollingInterval).subscribe((res) => {
      this.events = res;
    });

    if (this.scrolling) {
      setInterval(() => {

        if (this.table) {
          this.table.scroll();
        } else if (this.banner) {
          this.banner.scroll();
        }
      }, this.interval * 1000);
    }
  }

  ngOnDestroy() {

    this.sub.unsubscribe();
  }

  startPolling(interval: number = 30000): Observable<any> {

    return timer(0, interval)
      .pipe(
        //switchMap(() => this.icalService.getEvents('https://calendar.google.com/calendar/ical/4t0mtlq6irm5dl32gp9euutajg%40group.calendar.google.com/public/basic.ics')
        //switchMap(() => this.icalService.getEvents('https://calendar.lafayette.edu/category/24/events.ics')
        //switchMap(() => this.icalService.getEvents('https://www.shaker.org/ical/Shaker_Heights_Schools_export.ics')
        switchMap(() => this.icalService.getEvents(this.calUrl)
        ));
  }

  requestData(url: string): Observable<any> {

    return this.http
      .get(url);
  }
}
