import { AfterViewInit, Component, ElementRef, Input, OnInit, QueryList, SecurityContext, ViewChild, ViewChildren, ViewContainerRef } from '@angular/core';
import * as moment from 'moment-timezone';
import { MatHeaderRow, MatTable, MatTableDataSource } from '@angular/material/table';
import { PlayerClientService } from '@reveldigital/player-client';
import { gsap } from 'gsap';
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss']
})
export class TableComponent implements OnInit, AfterViewInit {

  @ViewChild('containerEl')
  containerEl: ElementRef;
  @ViewChild('table', { read: ElementRef })
  table: ElementRef;
  @ViewChild('header', { read: ElementRef })
  header: ElementRef;
  @ViewChildren("row", { read: ViewContainerRef })
  rows: QueryList<ViewContainerRef>;

  showHeader = true;
  rowColor = '';
  altColor = '';
  headerStyle: string;
  bodyStyle: string;
  y = 1;
  dy = 0;
  max = 0;
  visible = 0;
  displayedColumns: string[] = ['when', 'where', 'summary'];

  @Input() events: any;
  @Input() width: number;
  @Input() height: number;

  constructor(private domSanitizer: DomSanitizer) {

    if (typeof gadgets !== 'undefined') {
      let prefs = new gadgets.Prefs();
      this.showHeader = prefs.getBool('show-header');
      this.headerStyle = prefs.getString('header-style');
      this.bodyStyle = prefs.getString('body-style');
      this.rowColor = prefs.getString('row-color');
      this.altColor = prefs.getString('alt-color');

      this.y = this.showHeader ? 1 : 0;
    }
  }

  ngAfterViewInit(): void {

    if (this.headerStyle) {
      this.header.nativeElement.setAttribute('style',
        this.domSanitizer.sanitize(SecurityContext.HTML, this.domSanitizer.bypassSecurityTrustHtml(this.headerStyle.replace(/&#34;/g, "'"))))
    }
    if (this.bodyStyle) {
      this.table.nativeElement.setAttribute('style',
        this.domSanitizer.sanitize(SecurityContext.HTML, this.domSanitizer.bypassSecurityTrustHtml(this.bodyStyle.replace(/&#34;/g, "'"))))
    }
  }

  ngOnInit(): void {
  }


  getBackgroundColor(odd: boolean) {

    if (odd && this.altColor !== '') {
      return this.altColor;
    }
    return this.rowColor;
  }

  public scroll() {

    if (this.rows.length) {
      this.dy = this.rows.first.element.nativeElement.offsetHeight;
      this.visible = Math.floor(this.containerEl.nativeElement.offsetHeight / this.dy);
      this.max = this.rows.length - this.visible + 1;
    }

    this.y += 1;
    if (this.y >= this.max) {
      this.y = 0;
    }

    gsap.to(this.containerEl.nativeElement, {
      duration: 1.2, ease: "power4.inOut", scrollTo: '#item-' + this.y
    });
  }
}
